/* Modern Layout Styles */
.layout {
  display: flex;
  height: 100vh;
  width: 100vw;
  overflow: hidden;
  background: linear-gradient(135deg, var(--gray-50) 0%, var(--primary-50) 100%);
  font-family: var(--font-family-sans);
}

/* Sidebar */
.sidebar {
  width: 280px;
  background: linear-gradient(180deg, var(--gray-900) 0%, var(--gray-800) 100%);
  color: var(--white);
  padding: var(--space-6);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  box-shadow: var(--shadow-xl);
  transition: all var(--transition-normal);
  position: relative;
  z-index: 10;
}

.sidebar::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 100 100"><defs><pattern id="sidebar-grain" width="20" height="20" patternUnits="userSpaceOnUse"><circle cx="10" cy="10" r="0.5" fill="white" opacity="0.05"/></pattern></defs><rect width="100" height="100" fill="url(%23sidebar-grain)"/></svg>');
  opacity: 0.3;
}

.sidebar.collapsed {
  width: 80px;
}

.toggle-button {
  background: var(--primary-500);
  color: var(--white);
  border: none;
  padding: var(--space-2);
  border-radius: var(--radius-lg);
  cursor: pointer;
  margin-bottom: var(--space-6);
  transition: all var(--transition-fast);
  position: relative;
  z-index: 1;
}

.toggle-button:hover {
  background: var(--primary-600);
  transform: scale(1.05);
}

/* Menu Title */
.menu-title {
  font-size: var(--font-size-2xl);
  font-weight: var(--font-weight-bold);
  margin-bottom: var(--space-8);
  text-transform: uppercase;
  letter-spacing: 2px;
  transition: opacity var(--transition-normal);
  position: relative;
  z-index: 1;
  background: linear-gradient(135deg, var(--primary-300), var(--accent-300));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.sidebar.collapsed .menu-title {
  opacity: 0;
}

/* Sidebar Navigation */
.sidebar ul {
  list-style: none;
  padding: 0;
  margin: 0;
  position: relative;
  z-index: 1;
}

.sidebar ul li {
  margin-bottom: var(--space-2);
}

.sidebar ul li a {
  text-decoration: none;
  color: var(--gray-300);
  font-size: var(--font-size-base);
  font-weight: var(--font-weight-medium);
  display: flex;
  align-items: center;
  gap: var(--space-3);
  padding: var(--space-3) var(--space-4);
  border-radius: var(--radius-lg);
  transition: all var(--transition-normal);
  position: relative;
  overflow: hidden;
}

.sidebar ul li a::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255,255,255,0.1), transparent);
  transition: left 0.5s;
}

.sidebar ul li a:hover::before {
  left: 100%;
}

.sidebar ul li a:hover {
  background: linear-gradient(135deg, var(--primary-600), var(--primary-500));
  color: var(--white);
  transform: translateX(4px);
  box-shadow: var(--shadow-md);
}

.sidebar ul li a.active {
  background: linear-gradient(135deg, var(--primary-500), var(--primary-600));
  color: var(--white);
  box-shadow: var(--shadow-md);
}

.sidebar ul li a svg {
  font-size: var(--font-size-lg);
  flex-shrink: 0;
}

.sidebar.collapsed ul li a {
  justify-content: center;
  padding: var(--space-3);
}

.sidebar.collapsed ul li a svg {
  margin-right: 0;
}

/* Logout Button */
.logout-button {
  background: linear-gradient(135deg, var(--error-500), var(--error-600));
  color: var(--white);
  border: none;
  padding: var(--space-3) var(--space-4);
  border-radius: var(--radius-lg);
  cursor: pointer;
  margin-top: auto;
  display: flex;
  align-items: center;
  gap: var(--space-2);
  font-size: var(--font-size-sm);
  font-weight: var(--font-weight-medium);
  transition: all var(--transition-normal);
  position: relative;
  z-index: 1;
}

.logout-button:hover {
  background: linear-gradient(135deg, var(--error-600), var(--error-700));
  transform: translateY(-2px);
  box-shadow: var(--shadow-lg);
}

.sidebar.collapsed .logout-button {
  justify-content: center;
  padding: var(--space-3);
}

/* Main Content Area */
.main-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  padding: var(--space-6);
  background: transparent;
  overflow-y: auto;
  position: relative;
}

/* Top Header */
.top-header {
  background: linear-gradient(135deg, var(--white), var(--gray-50));
  color: var(--gray-800);
  padding: var(--space-6);
  border-radius: var(--radius-2xl);
  margin-bottom: var(--space-6);
  box-shadow: var(--shadow-lg);
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  overflow: hidden;
}

.top-header::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, var(--primary-500), var(--accent-500));
  opacity: 0.05;
}

.top-header h1 {
  margin: 0;
  font-size: var(--font-size-3xl);
  font-weight: var(--font-weight-bold);
  position: relative;
  z-index: 1;
  background: linear-gradient(135deg, var(--primary-600), var(--accent-600));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.user-info {
  display: flex;
  align-items: center;
  gap: var(--space-4);
  position: relative;
  z-index: 1;
}

.profile-link {
  background: linear-gradient(135deg, var(--primary-500), var(--primary-600));
  color: var(--white);
  padding: var(--space-2) var(--space-4);
  border-radius: var(--radius-full);
  text-decoration: none;
  font-size: var(--font-size-sm);
  font-weight: var(--font-weight-medium);
  display: flex;
  align-items: center;
  gap: var(--space-2);
  transition: all var(--transition-normal);
  box-shadow: var(--shadow-md);
}

.profile-link:hover {
  background: linear-gradient(135deg, var(--primary-600), var(--primary-700));
  transform: translateY(-2px);
  box-shadow: var(--shadow-lg);
}

/* Content Area */
.content {
  flex: 1;
  background: var(--white);
  border-radius: var(--radius-2xl);
  box-shadow: var(--shadow-lg);
  padding: var(--space-6);
  overflow-y: auto;
  position: relative;
}

/* Responsive Design */
@media (max-width: 1024px) {
  .sidebar {
    width: 240px;
  }
  
  .sidebar.collapsed {
    width: 70px;
  }
}

@media (max-width: 768px) {
  .layout {
    flex-direction: column;
  }

  .sidebar {
    width: 100%;
    height: auto;
    order: 1;
    padding: var(--space-4);
  }

  .sidebar.collapsed {
    width: 100%;
  }

  .sidebar ul {
    display: flex;
    flex-wrap: wrap;
    gap: var(--space-2);
  }

  .sidebar ul li {
    margin-bottom: 0;
  }

  .sidebar ul li a {
    padding: var(--space-2) var(--space-3);
    font-size: var(--font-size-sm);
  }

  .main-content {
    padding: var(--space-4);
    order: 2;
  }

  .top-header {
    padding: var(--space-4);
    margin-bottom: var(--space-4);
    flex-direction: column;
    gap: var(--space-4);
    text-align: center;
  }

  .top-header h1 {
    font-size: var(--font-size-2xl);
  }

  .content {
    padding: var(--space-4);
  }
}

@media (max-width: 480px) {
  .sidebar ul {
    flex-direction: column;
  }
  
  .sidebar ul li a {
    justify-content: flex-start;
  }
  
  .top-header h1 {
    font-size: var(--font-size-xl);
  }
}