/* Modern Tasks Styles */
.tasks-container {
  padding: var(--space-6);
  max-width: 1200px;
  margin: 0 auto;
  min-height: 100vh;
}

/* Header Section */
.tasks-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: var(--space-8);
  padding: var(--space-6);
  background: linear-gradient(135deg, var(--white), var(--gray-50));
  border-radius: var(--radius-2xl);
  box-shadow: var(--shadow-lg);
}

.header-content {
  flex: 1;
}

.page-title {
  display: flex;
  align-items: center;
  gap: var(--space-3);
  font-size: var(--font-size-4xl);
  font-weight: var(--font-weight-bold);
  color: var(--gray-800);
  margin: 0 0 var(--space-2) 0;
  background: linear-gradient(135deg, var(--primary-600), var(--accent-600));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.title-icon {
  color: var(--primary-500);
  font-size: var(--font-size-3xl);
}

.page-subtitle {
  font-size: var(--font-size-lg);
  color: var(--gray-600);
  margin: 0;
}

.header-stats {
  display: flex;
  gap: var(--space-4);
}

.stat-item {
  text-align: center;
  padding: var(--space-4);
  background: var(--primary-50);
  border-radius: var(--radius-xl);
  border: 2px solid var(--primary-100);
}

.stat-number {
  display: block;
  font-size: var(--font-size-3xl);
  font-weight: var(--font-weight-bold);
  color: var(--primary-600);
  line-height: 1;
}

.stat-label {
  font-size: var(--font-size-sm);
  color: var(--primary-500);
  font-weight: var(--font-weight-medium);
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

/* Search Section */
.search-section {
  margin-bottom: var(--space-8);
  position: relative;
}

.search-container {
  position: relative;
  max-width: 500px;
  margin: 0 auto;
}

.search-icon {
  position: absolute;
  left: var(--space-4);
  top: 50%;
  transform: translateY(-50%);
  color: var(--gray-400);
  font-size: var(--font-size-lg);
  z-index: 1;
}

.search-input {
  width: 100%;
  padding: var(--space-4) var(--space-4) var(--space-4) var(--space-12);
  border: 2px solid var(--gray-200);
  border-radius: var(--radius-full);
  font-size: var(--font-size-base);
  background: var(--white);
  transition: all var(--transition-normal);
  box-shadow: var(--shadow-sm);
}

.search-input:focus {
  outline: none;
  border-color: var(--primary-500);
  box-shadow: 0 0 0 3px rgb(14 165 233 / 0.1), var(--shadow-md);
}

.search-input::placeholder {
  color: var(--gray-400);
}

/* Suggestions */
.suggestions-container {
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
  max-width: 500px;
  z-index: var(--z-dropdown);
}

.suggestions-list {
  background: var(--white);
  border: 2px solid var(--gray-200);
  border-top: none;
  border-radius: 0 0 var(--radius-xl) var(--radius-xl);
  box-shadow: var(--shadow-lg);
  list-style: none;
  margin: 0;
  padding: var(--space-2);
}

.suggestion-item {
  display: flex;
  align-items: center;
  gap: var(--space-3);
  padding: var(--space-3);
  border-radius: var(--radius-lg);
  cursor: pointer;
  transition: all var(--transition-fast);
}

.suggestion-item:hover {
  background: var(--primary-50);
  color: var(--primary-600);
}

.suggestion-icon {
  color: var(--gray-400);
  font-size: var(--font-size-sm);
}

/* No Results */
.no-results {
  text-align: center;
  padding: var(--space-8);
  color: var(--gray-500);
}

.no-results-icon {
  font-size: var(--font-size-3xl);
  margin-bottom: var(--space-2);
  opacity: 0.6;
}

/* Tasks List Container */
.tasks-list-container {
  margin-bottom: var(--space-20);
}

.tasks-list {
  display: flex;
  flex-direction: column;
  gap: var(--space-3);
  max-width: 800px;
  margin: 0 auto;
}

/* Task List Item */
.task-list-item {
  background: var(--white);
  border-radius: var(--radius-xl);
  box-shadow: var(--shadow-sm);
  padding: var(--space-4);
  transition: all var(--transition-normal);
  animation: fadeIn 0.4s ease-out;
  animation-delay: var(--delay);
  animation-fill-mode: both;
  position: relative;
  overflow: hidden;
  border: 2px solid transparent;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: var(--space-4);
}

.task-list-item::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, var(--primary-500), var(--accent-500));
  opacity: 0;
  transition: opacity var(--transition-normal);
  z-index: 0;
}

.task-list-item:hover::before {
  opacity: 0.03;
}

.task-list-item:hover {
  transform: translateX(8px);
  box-shadow: var(--shadow-lg);
  border-color: var(--primary-200);
}

.task-list-item.completed {
  opacity: 0.6;
  background: var(--gray-50);
}

.task-list-item.completed::before {
  background: linear-gradient(135deg, var(--success-500), var(--success-600));
}

/* Task Number */
.task-number {
  width: 40px;
  height: 40px;
  background: linear-gradient(135deg, var(--primary-500), var(--primary-600));
  color: var(--white);
  border-radius: var(--radius-full);
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: var(--font-weight-bold);
  font-size: var(--font-size-lg);
  flex-shrink: 0;
  position: relative;
  z-index: 1;
}

.task-list-item.completed .task-number {
  background: linear-gradient(135deg, var(--success-500), var(--success-600));
}

/* Task Info */
.task-info {
  flex: 1;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  z-index: 1;
}

.task-main-info {
  flex: 1;
}

.task-title {
  font-size: var(--font-size-lg);
  font-weight: var(--font-weight-semibold);
  color: var(--gray-800);
  margin: 0 0 var(--space-1) 0;
  line-height: var(--line-height-tight);
}

.task-meta {
  display: flex;
  flex-direction: column;
  gap: var(--space-1);
}

.task-description {
  color: var(--gray-600);
  font-size: var(--font-size-sm);
  line-height: var(--line-height-relaxed);
  margin: 0;
}

.task-deadline {
  display: flex;
  align-items: center;
  gap: var(--space-2);
  color: var(--gray-500);
  font-size: var(--font-size-xs);
}

.deadline-icon {
  font-size: var(--font-size-xs);
}

/* Task Status Badge */
.task-status-badge {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: var(--space-2);
}

.status-indicator {
  width: 32px;
  height: 32px;
  border-radius: var(--radius-full);
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all var(--transition-fast);
}

.status-indicator.completed {
  background: var(--success-100);
  color: var(--success-600);
}

.status-indicator.pending {
  background: var(--warning-100);
  color: var(--warning-600);
}

.status-icon {
  font-size: var(--font-size-sm);
}

.priority-dot {
  width: 8px;
  height: 8px;
  border-radius: var(--radius-full);
}

.priority-dot.normal {
  background: var(--gray-400);
}

.priority-dot.urgent {
  background: var(--warning-500);
}

.priority-dot.overdue {
  background: var(--error-500);
}

/* Task Header */
.task-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: var(--space-4);
  position: relative;
  z-index: 1;
}

.task-status {
  display: flex;
  align-items: center;
  gap: var(--space-2);
}

.status-icon {
  font-size: var(--font-size-lg);
}

.status-icon.completed {
  color: var(--success-500);
}

.status-icon.pending {
  color: var(--warning-500);
}

.status-text {
  font-size: var(--font-size-sm);
  font-weight: var(--font-weight-medium);
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.status-text.completed {
  color: var(--success-600);
}

.status-text.pending {
  color: var(--warning-600);
}

.task-actions {
  display: flex;
  gap: var(--space-2);
  opacity: 0;
  transition: opacity var(--transition-fast);
}

.task-card:hover .task-actions {
  opacity: 1;
}

.action-btn {
  width: 32px;
  height: 32px;
  border: none;
  border-radius: var(--radius-lg);
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all var(--transition-fast);
  font-size: var(--font-size-sm);
}

.edit-btn {
  background: var(--primary-100);
  color: var(--primary-600);
}

.edit-btn:hover {
  background: var(--primary-500);
  color: var(--white);
  transform: scale(1.1);
}

.delete-btn {
  background: var(--error-100);
  color: var(--error-600);
}

.delete-btn:hover {
  background: var(--error-500);
  color: var(--white);
  transform: scale(1.1);
}

/* Task Body */
.task-body {
  margin-bottom: var(--space-4);
  position: relative;
  z-index: 1;
}

.task-title {
  font-size: var(--font-size-xl);
  font-weight: var(--font-weight-semibold);
  color: var(--gray-800);
  margin: 0 0 var(--space-2) 0;
  line-height: var(--line-height-tight);
}

.task-description {
  color: var(--gray-600);
  line-height: var(--line-height-relaxed);
  margin: 0;
}

/* Task Footer */
.task-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  z-index: 1;
}

.task-deadline {
  display: flex;
  align-items: center;
  gap: var(--space-2);
  color: var(--gray-500);
  font-size: var(--font-size-sm);
}

.deadline-icon {
  font-size: var(--font-size-xs);
}

.priority-indicator {
  width: 8px;
  height: 8px;
  border-radius: var(--radius-full);
}

.priority-indicator.normal {
  background: var(--gray-400);
}

.priority-indicator.urgent {
  background: var(--warning-500);
}

.priority-indicator.overdue {
  background: var(--error-500);
}

/* Task Edit Form */
.task-edit-form {
  position: relative;
  z-index: 1;
}

.form-group {
  margin-bottom: var(--space-4);
}

.form-group label {
  display: block;
  font-size: var(--font-size-sm);
  font-weight: var(--font-weight-medium);
  color: var(--gray-700);
  margin-bottom: var(--space-2);
}

.form-input {
  width: 100%;
  padding: var(--space-3);
  border: 2px solid var(--gray-200);
  border-radius: var(--radius-lg);
  font-size: var(--font-size-sm);
  transition: all var(--transition-fast);
  background: var(--white);
}

.form-input:focus {
  outline: none;
  border-color: var(--primary-500);
  box-shadow: 0 0 0 3px rgb(14 165 233 / 0.1);
}

.form-actions {
  display: flex;
  gap: var(--space-3);
  justify-content: flex-end;
}

/* Empty State */
.empty-state {
  grid-column: 1 / -1;
  text-align: center;
  padding: var(--space-16) var(--space-8);
  background: var(--white);
  border-radius: var(--radius-2xl);
  box-shadow: var(--shadow-md);
  border: 2px dashed var(--gray-200);
}

.empty-icon {
  font-size: var(--font-size-5xl);
  margin-bottom: var(--space-4);
  opacity: 0.6;
}

.empty-title {
  font-size: var(--font-size-2xl);
  font-weight: var(--font-weight-semibold);
  color: var(--gray-700);
  margin: 0 0 var(--space-2) 0;
}

.empty-description {
  font-size: var(--font-size-lg);
  color: var(--gray-500);
  margin: 0 0 var(--space-6) 0;
  max-width: 400px;
  margin-left: auto;
  margin-right: auto;
}

/* Floating Add Button */
.floating-add-task-button {
  position: fixed;
  bottom: 24px;
  right: 24px;
  background: linear-gradient(135deg, #3b82f6, #1d4ed8);
  color: white;
  border: none;
  border-radius: 50%;
  width: 64px;
  height: 64px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 24px;
  cursor: pointer;
  box-shadow: 0 10px 25px rgba(59, 130, 246, 0.3);
  transition: all 0.3s ease;
  z-index: 1000;
}

.floating-add-task-button:hover {
  background: linear-gradient(135deg, #1d4ed8, #1e40af);
  transform: scale(1.1);
  box-shadow: 0 15px 35px rgba(59, 130, 246, 0.4);
}

/* Task Detail Modal */
.task-detail-modal {
  background: var(--white);
  border-radius: var(--radius-2xl);
  width: 90%;
  max-width: 600px;
  max-height: 90vh;
  box-shadow: var(--shadow-2xl);
  animation: slideIn 0.3s ease-out;
  position: relative;
  overflow: hidden;
  z-index: 1000;
}

.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: var(--space-6);
  border-bottom: 2px solid var(--gray-100);
  background: linear-gradient(135deg, var(--primary-50), var(--accent-50));
}

.modal-title {
  display: flex;
  align-items: center;
  gap: var(--space-3);
  font-size: var(--font-size-2xl);
  font-weight: var(--font-weight-bold);
  color: var(--gray-800);
  margin: 0;
}

.title-icon {
  color: var(--primary-500);
  font-size: var(--font-size-xl);
}

.close-btn {
  width: 40px;
  height: 40px;
  border: none;
  background: var(--gray-100);
  color: var(--gray-600);
  border-radius: var(--radius-full);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: var(--font-size-xl);
  cursor: pointer;
  transition: all var(--transition-fast);
}

.close-btn:hover {
  background: var(--error-100);
  color: var(--error-600);
  transform: scale(1.1);
}

.modal-body {
  padding: var(--space-6);
  max-height: calc(90vh - 120px);
  overflow-y: auto;
}

/* Task Detail Content */
.task-detail-content {
  display: flex;
  flex-direction: column;
  gap: var(--space-6);
}

.task-status-section {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: var(--space-4);
  background: var(--gray-50);
  border-radius: var(--radius-xl);
}

.status-badge {
  display: flex;
  align-items: center;
  gap: var(--space-2);
}

.status-text {
  font-size: var(--font-size-sm);
  font-weight: var(--font-weight-medium);
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.status-text.completed {
  color: var(--success-600);
}

.status-text.pending {
  color: var(--warning-600);
}

.priority-badge {
  padding: var(--space-2) var(--space-3);
  border-radius: var(--radius-full);
  font-size: var(--font-size-xs);
  font-weight: var(--font-weight-medium);
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.priority-badge.normal {
  background: var(--gray-100);
  color: var(--gray-600);
}

.priority-badge.urgent {
  background: var(--warning-100);
  color: var(--warning-700);
}

.priority-badge.overdue {
  background: var(--error-100);
  color: var(--error-700);
}

.task-details {
  display: flex;
  flex-direction: column;
  gap: var(--space-4);
}

.detail-title {
  font-size: var(--font-size-3xl);
  font-weight: var(--font-weight-bold);
  color: var(--gray-800);
  margin: 0;
  line-height: var(--line-height-tight);
}

.detail-description {
  font-size: var(--font-size-lg);
  color: var(--gray-600);
  line-height: var(--line-height-relaxed);
  margin: 0;
}

.detail-meta {
  display: flex;
  flex-direction: column;
  gap: var(--space-3);
}

.meta-item {
  display: flex;
  align-items: center;
  gap: var(--space-3);
  padding: var(--space-3);
  background: var(--gray-50);
  border-radius: var(--radius-lg);
}

.meta-icon {
  color: var(--primary-500);
  font-size: var(--font-size-lg);
  flex-shrink: 0;
}

.meta-content {
  display: flex;
  flex-direction: column;
  gap: var(--space-1);
}

.meta-label {
  font-size: var(--font-size-xs);
  font-weight: var(--font-weight-medium);
  color: var(--gray-500);
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.meta-value {
  font-size: var(--font-size-sm);
  color: var(--gray-700);
  font-weight: var(--font-weight-medium);
}

/* Task Actions in Modal */
.task-actions {
  display: flex;
  gap: var(--space-3);
  justify-content: center;
  padding-top: var(--space-4);
  border-top: 2px solid var(--gray-100);
}

.task-actions .action-btn {
  padding: var(--space-3) var(--space-6);
  border: none;
  border-radius: var(--radius-lg);
  cursor: pointer;
  font-size: var(--font-size-sm);
  font-weight: var(--font-weight-medium);
  transition: all var(--transition-fast);
  display: flex;
  align-items: center;
  gap: var(--space-2);
}

.task-actions .edit-btn {
  background: linear-gradient(135deg, var(--primary-500), var(--primary-600));
  color: var(--white);
}

.task-actions .edit-btn:hover {
  background: linear-gradient(135deg, var(--primary-600), var(--primary-700));
  transform: translateY(-2px);
  box-shadow: var(--shadow-md);
}

.task-actions .delete-btn {
  background: linear-gradient(135deg, var(--error-500), var(--error-600));
  color: var(--white);
}

.task-actions .delete-btn:hover {
  background: linear-gradient(135deg, var(--error-600), var(--error-700));
  transform: translateY(-2px);
  box-shadow: var(--shadow-md);
}

/* Modal Styles */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 999;
  backdrop-filter: blur(4px);
  animation: fadeIn 0.3s ease-out;
}

.modal-content {
  background: var(--white);
  padding: var(--space-8);
  border-radius: var(--radius-2xl);
  width: 90%;
  max-width: 500px;
  box-shadow: var(--shadow-2xl);
  animation: slideIn 0.3s ease-out;
  position: relative;
}

.modal-content h2 {
  font-size: var(--font-size-2xl);
  font-weight: var(--font-weight-bold);
  color: var(--gray-800);
  margin: 0 0 var(--space-6) 0;
  text-align: center;
}

.modal-content input {
  width: 100%;
  padding: var(--space-4);
  margin-bottom: var(--space-4);
  border: 2px solid var(--gray-200);
  border-radius: var(--radius-lg);
  font-size: var(--font-size-base);
  font-family: var(--font-family-sans);
  transition: all var(--transition-fast);
  background: var(--white);
}

.modal-content input:focus {
  outline: none;
  border-color: var(--primary-500);
  box-shadow: 0 0 0 3px rgb(14 165 233 / 0.1);
}

.modal-buttons {
  display: flex;
  gap: var(--space-3);
  justify-content: center;
}

.modal-buttons button {
  padding: var(--space-3) var(--space-6);
  border: none;
  border-radius: var(--radius-lg);
  cursor: pointer;
  font-size: var(--font-size-sm);
  font-weight: var(--font-weight-medium);
  transition: all var(--transition-fast);
  display: flex;
  align-items: center;
  gap: var(--space-2);
}

.modal-buttons button:first-child {
  background: linear-gradient(135deg, var(--success-500), var(--success-600));
  color: var(--white);
}

.modal-buttons button:first-child:hover {
  background: linear-gradient(135deg, var(--success-600), var(--success-700));
  transform: translateY(-2px);
  box-shadow: var(--shadow-md);
}

.modal-buttons button:last-child {
  background: var(--gray-100);
  color: var(--gray-700);
  border: 2px solid var(--gray-200);
}

.modal-buttons button:last-child:hover {
  background: var(--gray-200);
  border-color: var(--gray-300);
}

/* Responsive Design */
@media (max-width: 768px) {
  .tasks-container {
    padding: var(--space-4);
  }
  
  .tasks-header {
    flex-direction: column;
    gap: var(--space-4);
    text-align: center;
  }
  
  .page-title {
    font-size: var(--font-size-3xl);
  }
  
  .tasks-list {
    gap: var(--space-2);
  }
  
  .task-list-item {
    padding: var(--space-3);
    gap: var(--space-3);
  }
  
  .task-number {
    width: 32px;
    height: 32px;
    font-size: var(--font-size-base);
  }
  
  .task-title {
    font-size: var(--font-size-base);
  }
  
  .task-description {
    font-size: var(--font-size-xs);
